*{
    font-family: sans-serif;
}

body{
    margin: 0;
    padding: 15px;
    box-sizing: border-box;
    min-height: 100vh;
    min-width: 100vw;
    background: #222;
    font-size: 16px;
    color: #fff;
}

h1{
    margin: 10px 0 20px 0;
    text-align: center;
}

label{
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
}

select, input{
    padding: 10px;
    width: 100%;
    color: #333;
    background: #fff;
    border: none;
    font-size: 100%;
    box-sizing: border-box;
    margin-bottom: 5px;
    option{
        color: #333;
    }
}

button, input[type="submit"]{
    padding: 7px 12px;
    color: #fff;
    background: rgb(97, 91, 95);
    font-size: 14px;
    border: none;
    margin: 5px 0;
    border-radius: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    transition: background-color 0.2s ease-out;
    outline: none;
    text-transform: uppercase;
    font-weight: bold;
    opacity: 0.6;

    &:hover, &:active{
        background-color: rgb(202, 83, 172);
        opacity: 0.9;
    }
}

.form-field{
    margin: 10px 0 15px 0;
}

.box{
    padding: 30px;
    margin: 20px auto;
    background: #333;
    border-radius: 10px;
    max-width: 400px;
    
}

.submission-btn, input[type="submit"].submission-btn{
    width: 200px;
    height: 50px;
    display: block;
    padding: 10px;
    margin: 20px auto 5px auto;
}

.loading .submission-btn, .loading input[type="submit"].submission-btn{
    height: 0;
    overflow: hidden;
    padding: 0;
    margin-bottom: 50px;
}

.setting-btn{
    background: none;
}

.settings{
    text-align: center;
    opacity: 0.7;
}

.sub-text{
    font-size: 13px;
    font-style: italic;
    opacity: 0.8;
}

#setup{
    text-align: center;
}

ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
    li{
        display: inline-block;
        background: #222;
        color: #fff;
        padding: 10px;
        margin: 5px;
        position: relative;
        
        button{
            text-align: center;
            margin: 0;
            padding: 0;
            color: #fff;
            position: absolute;
            top: -5px;
            right: -5px;
            background: rgb(179, 84, 136);
            border-radius: 50%;
            width: 15px;
            height: 15px;
            opacity: 1;

            &:before, &:after{
                content: '';
                position: absolute;
                width: 8px;
                height: 2px;
                background: #fff;
                top: 50%;
                left: 50%;
            }

            &:before{
                transform: translate(-50%, -50%) rotate(45deg);
            }

            &:after{
                transform: translate(-50%, -50%) rotate(-45deg);
            }
        }
    }
}

